  border-radius: 3px;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  transition: transform .3s cubic-bezier(.77, .2, .05, 1), background .3s cubic-bezier(.77, .2, .05, 1), opacity .5s;
  display: block;
  position: relative;
}

.MugiLa_menuToggle span:first-child {
  transform-origin: 0 0;
}

.MugiLa_menuToggle span:nth-last-child(2) {
  transform-origin: 0 100%;
}

.MugiLa_active span {
  opacity: 1;
  transform: rotate(-45deg)translate(-2px, -2px);
}

.MugiLa_active span:nth-last-child(2) {
  opacity: 0;
  transform: rotate(0)scale(.2);
}

.MugiLa_active span:nth-last-child(3) {
  transform: rotate(45deg)translate(2px, -1px);
}

